.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.covered{
  position: fixed;
  left: 20px;
  bottom: 20px;
  text-align: center;
  z-index: 1000;
}

@media screen and (max-width: 900px) {
    .covered{
      left: auto;
      bottom: auto;
      top: 10px;
      right: 3px;
      opacity: 0.5;
    }
}

.open-button {
    -webkit-appearance: none;
    position: relative;
    width: 100px;
    height: 50px;
    background-image: url(https://i.postimg.cc/857jHw2q/Screenshot-2020-04-16-at-1-07-06-PM.png);
    background-size: cover;
    border-radius: 50px;
    outline: none;
    cursor: pointer;

}
.open-button:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 50px;
  width: 50px;
  background: url(https://icon-library.com/images/moon-and-star-icon/moon-and-star-icon-9.jpg);
  background-size: cover ;
  border-radius: 50px;
}

@media screen and (max-width: 900px) {
  .open-button{
      width : 40px;
      height : 15px;
  }
  .open-button:before {
    width : 20px;
    height : 15px;
  }
}


.open-button:checked {
  background-image: url(https://i.postimg.cc/Hn0nstVK/Screenshot-2020-04-16-at-1-07-19-PM.png);
  background-color: green;

}

.open-button:checked:before {
  -webkit-transform: translate(100%);
          transform: translate(100%);
  background: url(https://png.pngtree.com/png-clipart/20190905/original/pngtree-sunny-to-cloudy-icon-png-image_4525767.jpg);
  background-size: cover ;
}

